<template>
  <router-view/>
</template>

<script>
document.body.addEventListener('click', () => {
  const tooltips = document.querySelectorAll('.tooltip__text')
  const tooltipBtns = document.querySelectorAll('.tooltip-btn')
  tooltips.forEach(tooltip => {
    tooltip.classList.remove('tooltip__text--active')
  })

  tooltipBtns.forEach(btn => {
    btn.classList.remove('tooltip-btn--active')
  })
})
</script>

<style lang="scss">
  @import "@/styles/styles.scss";
</style>
