<template>
  <div class="tab-export">
    <div class="export__input-wrap">
      <label for="archiveName" class="export__input-label">Имя архива:</label>
      <input type="text" class="export__input" id="archiveName" v-model="archiveName">
      <span class="export__archive-name">
        {{ archiveName ? `${archiveName}.zip` : ''}}
      </span>
    </div>
    <button class="btn export__btn" :disabled="!isLpReadyToExport" @click="exportLp">Экспорт</button>
    <a ref="downloadLink" class="download-link" href="#" hidden download="archive.zip"></a>
  </div>
</template>

<script>
import eventbus from '@/utils/eventbus'

const axios = require('axios')

export default {
  name: 'TabExport',
  data () {
    return {
      archiveName: ''
    }
  },
  methods: {
    exportLp () {
      const headerHtml = document.querySelector('.list-group__header').innerHTML
      const mainHtml = document.querySelector('.list-group__main').innerHTML
      const footerHtml = document.querySelector('.list-group__footer').innerHTML

      const compStylesList = [
        ...this.$store.state.headerList,
        ...this.$store.state.mainList,
        ...this.$store.state.footerList
      ]

      eventbus.$emit('toggleEditable')

      axios.get('/download', {
        params: {
          headerHtml,
          mainHtml,
          footerHtml,
          lpCssList: compStylesList,
          archiveName: this.archiveName
        }
      })
        .then(response => {
          console.log(response.data)
          this.$refs.downloadLink.href = response.data
          this.$refs.downloadLink.click()
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  computed: {
    isLpReadyToExport () {
      return (
        this.$store.state.headerList.length ||
        this.$store.state.mainList.length ||
        this.$store.state.footerList.length
      ) && this.archiveName
    }
  }
}
</script>

<style scoped>
  .export__input-wrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .export__input-label {
    font-size: 22px;
  }

  .export__input {
    width: 200px;
    margin-left: 20px;
    padding: 5px 10px;
    font-size: 22px;
    line-height: 1;
  }

  .export__archive-name {
    margin-left: 10px;
    font-size: 20px;
    line-height: 1;
    color: #aaa;
  }

  .export__btn {
    margin-top: 20px;
    font-size: 30px;
    font-weight: 700;
  }
</style>
