import store from '../store/index'

export function updateLocalStyles (table, newStyles) {
  for (let i = 0; i < table.rows.length; i++) {
    const row = table.rows[i]
    const cellName = row.cells[0].innerHTML.trim()

    switch (cellName) {
      case 'color':
      case 'fill':
      case 'background-color':
      case 'border-top-color':
      case 'border-left-color':
      case 'border-right-color':
      case 'border-bottom-color':
        store.state.colorPickers[cellName].setColor(newStyles[cellName])
        break

      case 'text-align':
      case 'text-transform':
      case 'border-top-style':
      case 'border-left-style':
      case 'border-right-style':
      case 'border-bottom-style':
      case 'background-position':
      case 'background-size':
        row.cells[1].children[0].value = newStyles[cellName]
        break

      case 'background-image':
        if (row.cells[1].children[0].value.startsWith('url')) {
          row.cells[1].children[0].value = newStyles[cellName].slice(5, -2)
        } else {
          row.cells[1].children[0].value = newStyles[cellName]
        }
        break

      case 'letter-spacing':
        row.cells[1].children[0].children[0].value = parseFloat(newStyles[cellName])
        break
    }
  }
}
