<template>
  <div class="tab-styles">
    <div class="structure">
      <h2 class="tab__heading">Структура LP</h2>
    </div>
    <div class="styles">
      <h2 class="tab__heading">Стили</h2>
      <table class="styles__table">
        <tr class="styles__row"
          v-for="(propValue, prop) in cssGroup"
          :key="Math.random() * prop.length"
        >
          <td class="styles__prop">{{ prop }}</td>
          <td class="styles__prop-value">
            <input
              class="styles__input"
              type="text"
              v-if="typeof propValue === 'string' && propValue !== 'color'"
              @change="changeStyleFromInput(prop, $event.target.value)"
            >
            <button class="color-picker" type="button" v-if="propValue === 'color'">Выбрать цвет</button>
            <input class="styles__input" type="number" v-if="propValue === 0">
            <div class="styles__number-input-wrap" v-if="propValue === 1">
              <input
                class="styles__input styles__number-input"
                type="number"
                @input="changeStyleFromInput(prop, $event.target.value)"
              >&nbsp;px
            </div>
            <select v-if="typeof propValue === 'object'">
              <option
                v-for="(value, valueIndex) in propValue"
                :value="value"
                :key="valueIndex"
                @click="changeStyleFromInput(prop, value)"
              >
                {{ value }}
              </option>
            </select>
          </td>
          <td class="styles__prop-tooltip">
            <button
              class="tooltip-btn"
              v-if="propValue === ''"
              @click="showTooltip($event)"
            >
              Показать доступные значения свойств
              <svg-icon className="tooltip-btn__icon" iconName="question"/>
              <span class="tooltip__text">{{ tooltips[prop] }}</span>
            </button>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { createLPStructure } from '@/utils/functions'
import { initColorPickers } from '@/utils/colorPickers'
import { updateLocalStyles } from '@/utils/updateLocalStyles'
import SvgIcon from '@/components/SvgIcon'

import eventbus from '@/utils/eventbus'
import cssGroup from '@/assets/propertiesDesignLite.json'
import tooltips from '@/assets/tooltipsLite.json'

export default {
  name: 'TabStyles',
  data () {
    return {
      tooltips,
      isTooltipActive: false
    }
  },
  methods: {
    updateStructure () {
      if (document.querySelector('.structure__list')) {
        document.querySelector('.structure__list').remove()
      }
      createLPStructure(document.querySelector('.preview-window'), document.querySelector('.structure'), true)
    },
    changeStyleFromInput (prop, value) {
      const elem = document.querySelector(this.$store.state.activeElemSelector)
      if (prop === 'background-image') {
        elem.style[prop] = `url("${value}")`
      } else if (prop === 'letter-spacing') {
        elem.style[prop] = `${value}px`
      } else {
        elem.style[prop] = value
      }
    },
    showTooltip (e) {
      e.stopPropagation()
      const target = e.target.closest('.tooltip-btn')
      target.classList.add('tooltip-btn--active')
      target.lastElementChild.classList.add('tooltip__text--active')
    }
  },
  computed: {
    activeElemStyles () {
      return this.$store.state.activeElemStyles
    },
    cssGroup () {
      return cssGroup
    }
  },
  watch: {
    activeElemStyles () {
      updateLocalStyles(document.querySelector('.styles__table'), this.activeElemStyles)
    }
  },
  mounted () {
    this.updateStructure()
    eventbus.$on('updateStructure', this.updateStructure)

    initColorPickers()
  },
  components: {
    'svg-icon': SvgIcon
  }
}
</script>

<style lang="scss">
  .tab-styles {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 20px;
  }

  .structure {
    padding-right: 30px;
    border-right: 1px solid #ccc;
  }

  .tab__heading {
    padding-left: 10px;
    margin: 0 0 20px;
    font-size: 20px;
    font-weight: 700;
    text-decoration: underline;
  }

  .structure__item {
    display: inline-block;
  }

  .structure__label {
    display: inline-block;
    padding: 5px 10px;
    transition: background-color 0.1s ease-in, color 0.1s ease-in;

    &:hover {
      background-color: $yellowgreen;
      color: #000;
      cursor: pointer;
    }
  }

  .structure__radiobtn:checked + .structure__label {
    background-color: $main-red;
    color: #fff;
  }

  .structure__list {
    padding: 0;

    & > .structure__list {
      padding-left: 15px;
    }

    & > &--nopadding {
      padding: 0;
    }
  }

  .styles {
    padding-left: 10px;
    transition: filter 0.2s ease-in;
  }

  .styles__table {
    width: 100%;
    border-collapse: collapse;

    & + .styles__table {
      margin-top: 30px;
    }
  }

  .styles__row:not(:last-child) {
    border-bottom: 1px solid #ccc;
  }

  .styles__row:nth-child(2n) {
    background-color: #f4f4f4;
  }

  .styles__prop,
  .styles__prop-value {
    padding: 5px 10px;
  }

  .styles__prop {
    position: relative;
    width: 40%;
  }

  .styles__prop--linked {
    position: relative;
    // transform: scale(1);

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: block;
      background-color: rgba(255, 255, 255, .7);
    }
  }

  .styles__prop--linked .styles__prop {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: 50%;
      right: 100%;
      z-index: -1;
      display: block;
      width: 10px;
      height: 110%;
      margin-right: 5px;
      border-bottom: 1px solid rgb(31, 189, 31);
      border-left: 1px solid rgb(31, 189, 31);
    }
  }

  .styles__link-btn-box {
    position: absolute;
    top: 50%;
    right: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin-right: 5px;
    transform: translateY(-50%);
    font-size: 0;
    line-height: 0;
    background-color: #fff;
  }

  .styles__link-btn-icon {
    width: 20px;
    height: 20px;
    fill: #ccc;
    transition: fill 0.1s ease-in;
  }

  .styles__link-btn {
    cursor: pointer;
  }

  .styles__link-checkbox + .styles__link-btn:hover .styles__link-btn-icon {
    fill: #999;
  }

  .styles__link-checkbox:checked + .styles__link-btn .styles__link-btn-icon {
    fill: rgb(31, 189, 31);
  }

  .styles__link-checkbox:checked + .styles__link-btn:hover .styles__link-btn-icon {
    fill: rgb(29, 117, 29);
  }

  .styles__prop-value {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
  }

  .styles__input {
    width: 50%;
  }

  .styles__unit-select {
    margin-left: 5px;
  }

  .pcr-button {
    border: 1px solid #ccc;
  }

  .tooltip-btn {
    position: relative;
    padding: 5px;
    margin: 0;
    font-size: 0;
    background-color: transparent;
    border: none;
    vertical-align: middle;
  }

  .tooltip-btn__icon {
    width: 20px;
    height: 20px;
    fill: #aaa;
    opacity: 0.5;
    transition: opacity 0.1s ease-in;
  }

  .tooltip-btn:hover .tooltip-btn__icon {
    opacity: 1;
  }

  .tooltip-btn--active .tooltip-btn__icon {
    opacity: 1;
    fill: #777;
  }

  .tooltip__text {
    position:absolute;
    z-index:10;
    top:100%;
    right:0;
    display: none;
    width:450px;
    padding:10px;
    font-size:12px;
    border:1px solid #aaa;
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
    background-color:#fff;

    &--active {
      display: inline-block;
    }
  }
</style>
